<template>
	<div class="is-preload">
		<!-- Wrapper -->
			<div id="wrapper">

				<!-- Main -->
					<div id="main">
						<div class="inner">

							<!-- Header -->
								<header id="header">
								<!-- vue-typed-js 自动打字 -->
									<el-tooltip content="sudo rm -rf /*" placement="bottom">
										<vue-typed-js :typeSpeed="100" :cursorChar="''" :strings="['install.icu 我们的初心，让每个开发都会运维！']">
											<span class="typing logo"></span>
										</vue-typed-js>
									</el-tooltip>
									<ul class="icons">
										<li><a href="#" class="icon brands fa-twitter"><span class="label">Twitter</span></a></li>
										<li><a href="#" class="icon brands fa-facebook-f"><span class="label">Facebook</span></a></li>
										<li><a href="#" class="icon brands fa-snapchat-ghost"><span class="label">Snapchat</span></a></li>
										<li><a href="#" class="icon brands fa-instagram"><span class="label">Instagram</span></a></li>
										<li><a href="#" class="icon brands fa-medium-m"><span class="label">Medium</span></a></li>
									</ul>
								</header>

							<!-- Feed -->
								<section>
									<div class="posts">
										<article v-if="feedIsOpen" class="list-box-mirror">
											<div @click="feedClick(item)" class="list-item" style="padding-left: 30px" v-for="(item, index) in feedItems" v-bind:key="index">
												<div class="item-tags">{{item.category_name}}</div>
													<div class="item-title">
														<div class="title-text">{{item.title}}</div>
													</div>
													<div class="item-status">
														<div class="status-circle success"></div>
														<div class="status-text">可用</div>
													</div>
													<div class="item-time">
														查看更多
													</div>
											</div>
										</article>
									</div>
								</section>

						</div>
					</div>

				<!-- Sidebar -->
					<div id="sidebar" :class="[meunOpen == true ? '':'inactive']">
						<div class="inner">
							<!-- Search -->
								<section id="search" class="alt">
									<form method="post" action="#">
										<input type="text" name="query" id="query" placeholder="Search" />
									</form>
								</section>

							<!-- Menu -->
								<nav id="menu">
									<header class="major">
										<h2>Menu</h2>
									</header>
									<ul>
										<!-- <li><a href="#">Homepage</a></li>
										<li><a href="#">Generic</a></li>
										<li><a href="#">Elements</a></li> -->
										<li class="menu-nav" v-for="(navItem, index) in navItems" v-bind:key="index">
											<span @click="navItem.is_show = !navItem.is_show" :class="[navItem.is_show  == true ? 'opener active':'opener']">{{ navItem.name }}</span>
											<ul>
												<li class="menu-cate" v-bind:key="index" @click="menuClick(navItem, '')"><a href="#">全部</a></li>
												<li class="menu-cate" v-for="(cateItem, index) in navItem.category" v-bind:key="index" @click="menuClick(navItem, cateItem)"><a href="#">{{ cateItem.name }}</a></li>
											</ul>
										</li>
										<li @click="getKeyClick" class="menu-nav"><a style="user-select: text" href="#">{{ keyContent }}</a></li>
										<li></li>
									</ul>
								</nav>

							<!-- Section -->
								<section>
									<header class="major">
										<h2>Feed list</h2>
									</header>
									<div class="mini-posts">
										<article v-if="feedIsOpen" class="list-box-mirror">
											<div @click="feedClick(item)" class="list-item" style="padding-left: 30px" v-for="(item, index) in feedItems" v-bind:key="index">
												<div class="item-tags">{{item.category_name}}</div>
													<div class="item-title">
														<div class="title-text">{{item.title}}</div>
													</div>
													<div class="item-status">
														<div class="status-circle success"></div>
														<div class="status-text">可用</div>
													</div>
													<div class="item-time">
														查看更多
													</div>
											</div>
										</article>
									</div>
								</section>

							<!-- Footer -->
								<footer id="footer">
									<p class="copyright">&copy; Untitled. All rights reserved. Demo Images: <a href="https://unsplash.com">Unsplash</a>. Design: <a href="https://html5up.net">HTML5 UP</a>.</p>
								</footer>
						</div>
            <a @click="MenuOpen" href="#sidebar" class="toggle">Toggle</a>
					</div>
			</div>
			<router-view/>
  </div>
</template>

<script>
import { GetNavigation } from '@/api/api'
import { GetFeedByTag } from '@/api/api'

export default {
  data() {
    return {
			navItems: [],
      feedItems: [], // feed流数据
			feedIsOpen: true, // true则显示feed流
			meunOpen: false,
			keyContent: '获取KEY'
    }
	},
	mounted() {
    this.getNavigation()
	},
	methods: {
    MenuOpen() {
      this.meunOpen = !this.meunOpen;
		},
		getKeyClick() {
			if (this.keyContent == '获取KEY') {this.keyContent = 'YUEEOKHGXDROKF';return}
			if (this.keyContent == 'YUEEOKHGXDROKF') {this.keyContent = '获取KEY'}
		},
    feedClick(item) {
      // push 到详情页
      this.$router.push({
        path:'/m/detail',
        query: {item: JSON.stringify(item)}
      });
		},
		menuClick(navItem, cateItem) {
			// this.MenuOpen()
      var item = {"navItem": navItem, "cateItem": cateItem};
      this.getFeed(item)
		},
    getNavigation() {
      GetNavigation().then(response => {
        if (response.data.code === 0 && response.data.msg === "ok") {
          var navigation = response.data.data.navigation 
          if (navigation == null) {
            this.navItems = []
            return
					}
					for (var key in navigation ) {
						navigation[key].is_show = false
					}
					this.navItems = navigation // 导航列表
          var navItem = {"navItem":navigation[0], "cateItem": ""}
          this.getFeed(navItem)
        }
      }).catch(err => {
        this.errMsg('获取分类列表时出错 ', err)
      })
		},
    getFeed(item){
      if (item.cateItem.tag) {
        this.getFeedByTag(item.navItem.tag, item.cateItem.tag); return
      }
      this.getFeedByTag(item.navItem.tag, '')
    },
    getFeedByTag(navigationTag, categoryTag) {
      GetFeedByTag(navigationTag, categoryTag).then(response => {
        if (response.data.code === 0 && response.data.msg === "ok") {
          if (response.data.data.feed == null) {
            this.feedItems = []
            return
          }
          this.feedItems = response.data.data.feed
          return
        }
      }).catch(err => {
        this.errMsg('获取文章列表失败 ', err)
      })
		},
    errMsg(msg, err) {
      this.$message({
        type: 'error',
        message: msg + err
      });
    },
  },
}
</script>

<style scoped>
@import "../../static/mobile/css/main.css";

div:focus{
	outline: none;
}

/* index */
#header {
  margin-bottom: 10px;
}
#header .logo {
	font-size: 1.15em;
	margin: 0;
}

/* menu */
#sidebar > .inner > .alt {
	background-color: #eff1f2;
	border-bottom: 0;
	margin: -2.22222em 0 4.44444em -2.22222em;
	padding: 1.92222em;
	width: calc(100% + 4.44444em);
}

.menu-nav {
	font-size: 1.4em;
  padding: 0.71em 0;
}
.menu-cate {
	font-size: 0.9em;
}
#sidebar > .inner > * {
	border-bottom: none;
	padding: 0;
}
#menu > ul > li > ul a, #menu > ul > li > ul span {
  font-size: 1.0em;
}
#menu ul a, #menu ul span {
	font-size: 1.1em;
  padding: 0.41em 0;
}

/* Feed */
#main > .inner > section {
    padding: 1em 0 1em 0;
}
.list-box-mirror:hover {cursor: pointer}
.list-box-mirror {
	width: 100%;
}
.list-item {
  height: 115px;
  border: 1px solid #eaeaea;
	position: relative;
	border-radius: 5px;
}
.item-tags {
  position: absolute;
  left: 0;
  top: 0;
  width: 30px;
  height: 17px;
  background: #ff6a00;
  border-radius: 0 0 2px 0;
  font-family: PingFangSC-Medium;
  font-size: 12px;
  color: #fff;
  letter-spacing: 0;
  text-align: center;
  line-height: 17px;
  overflow: hidden;
}
.item-title {
  height: 25px;
}
.item-status {
  overflow: hidden;
  letter-spacing: 0;
  height: 24px;
  line-height: 24px;
  margin: 3px 0 2px;
}
.success {
  background: #50be61;
}
.status-circle {
  float: left;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  margin-top: 10px;
  margin-right: 10px;
}
.status-text {
  float: left;
  font-size: 14px;
  color: #373d41;
}
.item-time {
  font-size: 12px;
  color: #73777a;
  letter-spacing: 0;
  line-height: 24px;
}
.list-item:hover {
  box-shadow: 0 2px 15px 0 rgba(0,0,0,.15);
}
.list-item .title-text {
	margin-top: 20px;
	font-size: 16px;
}
.list-item:hover .item-title .title-text {
  color: #ff6a00;
}

/* 去除a 标签默认样式*/
/*包含以下四种的链接*/
a {
  text-decoration: none;
}
/*正常的未被访问过的链接*/
a:link {
  text-decoration: none;
}
/*已经访问过的链接*/
a:visited {
  text-decoration: none;
}
/*鼠标划过(停留)的链接*/
a:hover {
  text-decoration: none;
}
/* 正在点击的链接*/
a:active {
  text-decoration: none;
}
</style>
